import IndexLayout from '../layouts';
import Wrapper from '../components/Wrapper';
import SiteNav from '../components/header/SiteNav';
import { SiteHeader, outer, inner, SiteMain } from '../styles/shared';
import * as React from 'react';
import { css } from '@emotion/core';

import { PostFullHeader, PostFullTitle, NoImage, PostFull } from '../templates/post';
import { PostFullContent } from '../components/PostContent';
import Footer from '../components/Footer';
import Helmet from 'react-helmet';
import 'react-responsive-carousel/lib/styles/carousel.min.css';
import { Carousel } from 'react-responsive-carousel';
import img1 from '../content/photos/ILK_8490.jpg';
import img2 from '../content/photos/IMG_5969.jpg';
import img3 from '../content/photos/IMG_5971.jpg';
import img4 from '../content/photos/IMG_5977.jpg';
import img5 from '../content/photos/IMG_5983.jpg';

const PageTemplate = css`
  .site-main {
    background: #fff;
    padding-bottom: 4vw;
  }
`;

class DemoCarousel extends React.Component {
  render() {
    return (
      <Carousel>
        <div>
          <img src={img1} />
          <p className="legend">Kurucularmz</p>
        </div>
        <div>
          <img src={img2} />
          <p className="legend">Resepsiyon</p>
        </div>
        <div>
          <img src={img3} />
          <p className="legend"> Mekan 1</p>
        </div>
        <div>
        <img src={img4} />
          <p className="legend"> Mekan 2</p>
        </div>
        <div>
        <img src={img5} />
          <p className="legend"> Mekan 3</p>
        </div>
      </Carousel>
    );
  }
}

const About: React.FunctionComponent = () => (
  <IndexLayout>
    <Helmet>
      <title>Hakkmzda</title>
    </Helmet>
    <Wrapper css={PageTemplate}>
      <header css={[outer, SiteHeader]}>
        <div css={inner}>
          <SiteNav />
        </div>
      </header>
      <main id="site-main" className="site-main" css={[SiteMain, outer]}>
        <article className="post page" css={[PostFull, NoImage]}>
          <PostFullHeader>
            <PostFullTitle>Hakkmzda</PostFullTitle>
          </PostFullHeader>

          <PostFullContent className="post-full-content">
            <div className="post-content">
              <p>
                zel Rozet Aile Danma Merkezi, Uzman Klinik Psikolog Gkin aylar ve Uzman
                Psikolog Zeliha zata tarafndan 2018 ylnda kurulmutur. 2007 ylnda yollar
                Uluda niversitesi Hastanesinde kesien iki psikolog iki yl ayn hastanede
                almlardr. Yollar yllar sonra ayn dnemde dnyaya getirdikleri ocuklar ile
                birlikte mesleki hayatlarna farkl bir yn verme isteklerinin ortaklnda tekrar
                kesimitir.{' '}
              </p>
              <p>
                zel Rozet Aile Danma Merkezi, psikolojinin daha geni kitlelere yaylmas iin
                aile danmanl, bireysel danmanlk ve kurumsal danmanlk alanlarnda gncel
                bilimsel bilgilerin nda hizmet vermeyi amalamaktadr.
              </p>
            </div>
            <DemoCarousel />
          </PostFullContent>
        </article>
      </main>
      <Footer />
    </Wrapper>
  </IndexLayout>
);

export default About;
